import React, { useContext, useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import DoneIcon from '@material-ui/icons/Done';
import { Paper, TextField, IconButton } from '@material-ui/core';
import { UserContext } from '../../config/UserContext';
import { firestore } from '../../config/firebase';
import configDB from '../../config/firebase';
import { Autocomplete } from '@material-ui/lab';
import { useDispatch } from 'react-redux';
import { show, changeMessage, changeStatus } from '../snackBar/snackBarSlice';
import { Redirect } from 'react-router-dom';
import BackIcon from '@material-ui/icons/ArrowBackIos';

const PhotoConfig = ({ onPageChange, blob }) => {
  const currentUser = useContext(UserContext);
  const classes = useStyles();
  const [imageUrl, setImageUrl] = useState();
  const [description, setDescription] = useState('');
  const [projects, setProjects] = useState([]);
  const [project, setProject] = useState('');
  const [redirect, setRedirect] = useState(false);
  const dispatch = useDispatch();

  const savePost = () => {
    if (imageUrl) {
      if (project === '') {
        dispatch(changeMessage('Escolha um projeto para continuar!'));
        dispatch(changeStatus('error'));
        dispatch(show());
      } else {
        var formdata = new FormData();
        formdata.append('picture', blob, 'blob');

        var requestOptions = {
          method: 'POST',
          body: formdata,
          redirect: 'follow',
        };

        fetch('http://127.0.0.1:5000/faces/identify', requestOptions)
          .then(async (response) => {
            let data = await response.json();
            firestore
              .collection('post')
              .add({
                date: new Date(),
                description: description,
                photo: imageUrl,
                project: project.id,
                uid: currentUser.uid,
                personsCoordinates: data.data,
              })
              .then(function (docRef) {
                dispatch(changeMessage('Seu post foi publicado com sucesso!'));
                dispatch(changeStatus('success'));
                dispatch(show());
                setRedirect(true);
              })
              .catch(function (error) {
                dispatch(
                  changeMessage('Não foi possível publicar seu post :(')
                );
                dispatch(changeStatus('error'));
                dispatch(show());
              });
          })
          .then((result) => {
            console.log('certo');
          })
          .catch((error) => console.log('error', error));
      }
    }
  };

  useEffect(() => {
    if (blob) {
      const storageRef = configDB.storage().ref();
      const url = URL.createObjectURL(blob).split('/');
      const name = url[url.length - 1];
      const fileRef = storageRef.child(name);
      fileRef
        .put(blob)
        .then(() => {
          console.log('Uploaded file', '' + name);
        })
        .then(() => {
          fileRef.getDownloadURL().then((url) => {
            setImageUrl(url);
          });
        });
    }

    firestore
      .collection('projects')
      .get()
      .then((snapshot) => {
        if (snapshot) {
          let data = [];
          snapshot.forEach((item) =>
            data.push({ id: item.id, name: item.data().project })
          );
          setProjects(data);
        }
      });
  }, [blob]);

  return (
    <>
      {redirect ? <Redirect push to="/" /> : null}
      {blob && (
        <Paper className={classes.imgBox}>
          <img
            src={URL.createObjectURL(blob)}
            alt={'snap'}
            className={classes.img}
          ></img>
        </Paper>
      )}

      <div className={classes.div}>
        <TextField
          id="description"
          label="Descrição"
          variant="outlined"
          fullWidth
          className={classes.textButton}
          onChange={(e) => setDescription(e.target.value)}
        />
      </div>
      <div className={classes.div}>
        {projects && (
          <Autocomplete
            className={classes.textButton}
            fullWidth
            value={project}
            onChange={(e, value) => {
              setProject(value);
            }}
            getOptionSelected={(option, value) => option.value === value.value}
            options={projects}
            getOptionLabel={(option) => option.name || ''}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Selecione um projeto"
                variant="outlined"
              ></TextField>
            )}
          ></Autocomplete>
        )}
      </div>
      <div className={classes.div}>
        <div className={classes.buttonDiv}>
          <IconButton
            aria-label="voltar"
            onClick={() => {
              onPageChange('Camera');
            }}
          >
            <BackIcon />
          </IconButton>
        </div>
        <div className={classes.buttonDiv}>
          <IconButton aria-label="delete" onClick={savePost}>
            <DoneIcon className={classes.doneButton} />
          </IconButton>
        </div>
      </div>
    </>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: 'hidden',
    height: '100%',
    textAlign: 'center',
    flexDirection: 'column',
    marginBottom: '60px',
    padding: '4vh 1vh',
  },
  imgBox: {
    maxWidth: '100%',
    maxHeight: '80%',
    margin: '10px',
    display: 'flex',
    justifyContent: 'center',
  },
  img: {
    height: 'inherit',
    maxWidth: 'inherit',
  },
  input: {
    display: 'none',
  },
  paper: {
    flex: 1,
    flexDirection: 'column',
    padding: '10px 10px 0px 10px',
  },
  mainPaper: {
    padding: theme.spacing(2),
    margin: 'auto',
    maxWidth: 500,
  },
  photoButton: {
    margin: '5px 5px 10px 5px',
    backgroundColor: '#db826e',
    color: '#ffffff',
    '&:hover': {
      backgroundColor: '#db826e',
      color: '#ffffff',
    },
  },
  textButton: {
    margin: '5px',
  },
  doneButton: {
    margin: '5px',
    fill: '#04bd0d',
  },
  buttonDiv: {
    flex: 1,
  },
  typography: {
    margin: '15px',
  },
  alert: {
    margin: '15px 0px 0px 0px',
  },
  label: {
    display: 'contents',
  },
  div: {
    display: 'flex',
  },
  media: {
    height: 200,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    width: '100%',
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  snack: {
    height: '100%',
  },
}));

export default PhotoConfig;
