import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';

import Home from './features/home/Home';
import Profile from './features/profile/Profile';
import CreatePost from './features/camera/CreatePost';
import Login from './features/login/Login';
import AppBar from './features/appBar/AppBar';
import NavigationBar from './features/navigationBar/NavigationBar';
import Register from './features/register/Register';

import { MobileView, isMobile } from 'react-device-detect';

import { auth } from './config/firebase';
import { useAuthState } from 'react-firebase-hooks/auth';
import { useSelector, useDispatch } from 'react-redux';
import { selectSnackBar, hide } from './features/snackBar/snackBarSlice';

import Alert from '@material-ui/lab/Alert';
import { Snackbar } from '@material-ui/core';
import bgImage from './assets/arvores.svg';

function App() {
  const [user] = useAuthState(auth);
  const snackBar = useSelector(selectSnackBar);
  const dispatch = useDispatch();
  const classes = useStyles();

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    dispatch(hide());
  };

  return (
    <div className={classes.background}>
      <Snackbar
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        open={snackBar.open}
        autoHideDuration={6000}
        onClose={handleClose}
        className={classes.snack}
      >
        <Alert
          className={classes.alert}
          variant="filled"
          severity={snackBar.status}
        >
          {snackBar.message}
        </Alert>
      </Snackbar>
      <Router>
        {user ? (
          <>
            <AppBar />
            <MobileView>
              <NavigationBar />
            </MobileView>
            <div className={classes.page}>
              <Switch>
                <Route path="/profile">
                  <Profile />
                </Route>
                <Route path="/camera">
                  <CreatePost />
                </Route>
                <Route path="/register">
                  <Register />
                </Route>
                <Route path="/login"></Route>
                <Route path="/config"></Route>
                <Route path="/">
                  <Home />
                </Route>
              </Switch>
            </div>
          </>
        ) : (
          <Login />
        )}
      </Router>
    </div>
  );
}

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
  },
  background: {
    backgroundImage: isMobile ? '' : `url('${bgImage}')`,
    backgroundPosition: 'left 50px bottom',
    backgroundRepeat: 'no-repeat',
    height: '100vh',
    backgroundAttachment: 'fixed',
    overflow: 'auto',
  },
  snack: {
    marginTop: isMobile ? '17%' : '2%',
  },
  page: {
    marginTop: '60px',
  },
});

export default App;
