import React, { useContext, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Avatar,
  Chip,
  IconButton,
  Paper,
  Typography,
  Button,
} from '@material-ui/core';
import PeopleIcon from '@material-ui/icons/People';
import { UserContext } from '../../config/UserContext';
import { auth, firestore } from '../../config/firebase';
import { MobileView } from 'react-device-detect';

const Profile = () => {
  const currentUser = useContext(UserContext);
  const [projects, setProjects] = useState();
  const [projectsNames, setProjectsNames] = useState([]);

  useEffect(() => {
    if (currentUser) {
      firestore
        .collection('users')
        .doc(currentUser.uid)
        .get()
        .then((doc) => {
          if (doc) {
            if (doc.data()) {
              setProjects(doc.data().projects);
            }
          }
        });
    }
  }, [currentUser]);

  useEffect(() => {
    if (projects) {
      let data = [];

      firestore
        .collection('projects')
        .get()
        .then((docs) => {
          if (docs) {
            docs.forEach((doc) => {
              if (projects.includes(doc.id)) {
                data.push(doc.data().project);
              }
            });
            setProjectsNames(data);
          }
        });
    }
  }, [projects]);

  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <div className={classes.avatarDiv}>
          <Avatar
            alt="Remy Sharp"
            src={currentUser && currentUser.photoURL}
            className={classes.avatar}
          />
        </div>
        <Typography variant="h6" color="textSecondary" component="p">
          {currentUser && currentUser.displayName}
        </Typography>
        <Typography variant="caption" color="textSecondary" component="p">
          {currentUser && currentUser.email}
        </Typography>

        <Typography variant="h6" color="textSecondary" component="p">
          <IconButton className={classes.icon} aria-label="add to favorites">
            <PeopleIcon />
          </IconButton>
          Alice Lara
        </Typography>
        <div className={classes.chipList}>
          {projectsNames &&
            projectsNames.map((data) => {
              return (
                <Chip
                  key={data}
                  className={classes.chip}
                  size="small"
                  label={'#' + data}
                />
              );
            })}
        </div>
        <MobileView>
          <div className={classes.div}>
            <Button
              component="span"
              variant="contained"
              className={classes.photoButton}
              fullWidth
              onClick={() => auth.signOut()}
            >
              Sair
            </Button>
          </div>
        </MobileView>
      </Paper>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: 'hidden',
    padding: '4vh 1vh',
    height: '100%',
    verticalAlign: 'middle',
  },
  paper: {
    padding: theme.spacing(2),
    margin: 'auto',
    maxWidth: 500,
    textAlign: 'center',
  },
  avatar: {
    width: theme.spacing(15),
    height: theme.spacing(15),
  },
  avatarDiv: {
    alignItems: 'center',
    display: 'flex',
    justifyContent: 'center',
    marginBottom: '2vh',
    marginTop: '5vh',
  },
  icon: {
    color: '#F58F6E',
  },
  chip: {
    backgroundColor: '#f7ac97',
    margin: '0.5vh 1vh 0.5vh 0',
    alignItems: 'center',
  },
  chipList: {
    display: 'flex',
    justifyContent: 'left',
    flexWrap: 'wrap',
    padding: '1vh 2vh 1vh 0',
  },

  div: {
    display: 'flex',
  },
  photoButton: {
    margin: '5px 5px 10px 5px',
    backgroundColor: '#db826e',
    color: '#ffffff',
    '&:hover': {
      backgroundColor: '#db826e',
      color: '#ffffff',
    },
  },
}));

export default Profile;
