import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { TextField, IconButton } from '@material-ui/core';
import DoneIcon from '@material-ui/icons/Done';
import BackIcon from '@material-ui/icons/ArrowBackIos';
import { firestore } from '../../config/firebase';
import { useDispatch } from 'react-redux';
import { show, changeMessage, changeStatus } from '../snackBar/snackBarSlice';
import { Redirect } from 'react-router-dom';

const RegisterProject = ({ onPageChange }) => {
  const [project, setProject] = useState('');
  const [redirect, setRedirect] = useState(false);
  const dispatch = useDispatch();

  const savePost = () => {
    firestore
      .collection('projects')
      .add({
        project: project,
      })
      .then(function (docRef) {
        dispatch(changeMessage('O projeto foi cadastrado com sucesso!'));
        dispatch(changeStatus('success'));
        dispatch(show());
        setRedirect(true);
      })
      .catch(function (error) {
        console.error('Error adding document: ', error);
      });
  };

  const classes = useStyles();
  return (
    <div className={classes.root}>
      {redirect ? <Redirect push to="/" /> : null}
      <div className={classes.div}>
        <TextField
          id="description"
          label="Projeto"
          variant="outlined"
          fullWidth
          className={classes.textButton}
          onChange={(e) => setProject(e.target.value)}
        />
      </div>

      <div className={classes.div}>
        <div className={classes.buttonDiv}>
          <IconButton aria-label="voltar" onClick={() => onPageChange('')}>
            <BackIcon />
          </IconButton>
        </div>
        <div className={classes.buttonDiv}>
          <IconButton aria-label="delete" onClick={savePost}>
            <DoneIcon className={classes.doneButton} />
          </IconButton>
        </div>
      </div>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: '10px',
  },
  div: {
    display: 'flex',
  },
  doneButton: {
    margin: '5px',
    fill: '#04bd0d',
  },
  buttonDiv: {
    flex: 1,
  },
  textButton: {
    margin: '10px',
  },
  alert: {
    margin: '15px 0px 15px 0px',
  },
}));

export default RegisterProject;
