import React, { useContext, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { BrowserView, isMobile } from 'react-device-detect';
import { Avatar, Paper, Typography } from '@material-ui/core';
import Post from '../post/Post';
import { auth, firestore } from '../../config/firebase';
import { UserContext } from '../../config/UserContext';

import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import CommentDialog from '../comment/CommentDialog';

const Home = () => {
  const currentUser = useContext(UserContext);
  const { uid } = auth.currentUser;

  const [projects, setProjects] = useState([]);
  const [projectsNames, setProjectsNames] = useState([]);

  const [admin, setAdmin] = useState();

  const [children, setChildren] = useState([]);
  const [child, setChild] = useState('');
  const [childrenNames, setChildrenNames] = useState([]);

  const [posts, setPosts] = useState([]);
  const [project, setProject] = useState('');
  const [dialog, setDialog] = useState(false);
  const [currentPost, setCurrentPost] = useState();

  // buscar os projetos que estão cadastrados no usuario
  useEffect(() => {
    firestore
      .collection('users')
      .doc(uid)
      .get()
      .then((doc) => {
        if (doc) {
          if (doc.data()) {
            setProjects(doc.data().projects);
            setAdmin(doc.data().admin);
            setChildren(doc.data().children);
          }
        }
      });
  }, [uid]);

  useEffect(() => {
    let data = [];
    firestore
      .collection('projects')
      .get()
      .then((docs) => {
        if (docs) {
          docs.forEach((doc) => {
            if (projects.includes(doc.id)) {
              data.push({ name: doc.data().project, id: doc.id });
            }
          });
          setProjectsNames(data);
        }
      });
  }, [projects]);

  useEffect(() => {
    let data = [];
    firestore
      .collection('children')
      .get()
      .then((docs) => {
        if (docs) {
          docs.forEach((doc) => {
            if (children && doc.id && children.includes(doc.id)) {
              data.push({ name: doc.data().name, id: doc.id });
            }
          });
          setChildrenNames(data);
        }
      });
  }, [children]);

  // buscando posts para casa usuario
  useEffect(() => {
    if (admin) {
      firestore
        .collection('post')
        .where('uid', '==', uid)
        .get()
        .then((snapshot) => {
          if (snapshot) {
            let data = [];
            snapshot.forEach((item) => {
              data.push({ ...item.data(), id: item.id });
            });
            setPosts(data);
          }
        });
    } else if (projects && projects.length > 0) {
      firestore
        .collection('post')
        .where('project', 'in', projects)
        .get()
        .then((snapshot) => {
          if (snapshot) {
            let data = [];
            snapshot.forEach((item) =>
              data.push({ ...item.data(), id: item.id })
            );
            setPosts(data);
          }
        });
    }
  }, [projects, admin, uid]);

  const handleProjectName = (projectId) => {
    return projectsNames.length > 0
      ? projectsNames.filter((p) => p.id === projectId)[0].name
      : '';
  };

  const handleOpenDialog = (postId) => {
    setCurrentPost(postId);
    setDialog(!dialog);
  };

  const saveComment = (comment) => {
    if (comment !== '') {
      firestore.collection('comments').add({
        postId: currentPost,
        uid: currentUser.uid,
        user: currentUser.email.split('@')[0],
        comment: comment,
      });
    }
    setDialog(!dialog);
  };

  const handleCloseDialog = () => {
    setDialog(!dialog);
  };
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <CommentDialog
        state={dialog}
        handleCloseDialog={handleCloseDialog}
        saveComment={saveComment}
      ></CommentDialog>
      <BrowserView style={{ flex: 2 }}>
        <div className={classes.profile}>
          <div className={classes.avatarDiv}>
            <Avatar
              alt="Remy Sharp"
              src={currentUser && currentUser.photoURL}
              className={classes.avatar}
            />
          </div>
          <Typography variant="h6" color="textSecondary" component="p">
            {currentUser && currentUser.displayName}
          </Typography>
        </div>
      </BrowserView>

      <div style={{ flex: 3 }}>
        <Paper className={classes.paper}>
          <div className={classes.select}>
            <FormControl
              variant="outlined"
              size="small"
              className={classes.formControl}
            >
              <InputLabel id="demo-simple-select-outlined-label">
                Projeto
              </InputLabel>
              <Select
                labelId="demo-simple-select-outlined-label"
                id="demo-simple-select-outlined"
                value={project}
                onChange={(event) => setProject(event.target.value)}
                label="Projeto"
              >
                <MenuItem value="">
                  <em>Nenhum</em>
                </MenuItem>
                {projectsNames.map((project, index) => (
                  <MenuItem key={index} value={project.id}>
                    {project.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <FormControl
              variant="outlined"
              size="small"
              className={classes.formControl}
            >
              <InputLabel id="demo-simple-select-outlined-child">
                Criança
              </InputLabel>
              <Select
                labelId="demo-simple-select-outlined-child"
                id="demo-simple-select-outlined"
                value={child}
                onChange={(event) => setChild(event.target.value)}
                label="Crianças"
              >
                <MenuItem value="">
                  <em>Nenhum</em>
                </MenuItem>
                {childrenNames.map((child, index) => (
                  <MenuItem key={index} value={child.id}>
                    {child.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </div>
          <div style={{ overflow: 'auto', maxHeight: '75vh' }}>
            {posts.length > 0 ? (
              posts
                .filter((p) => (project !== '' ? p.project === project : true))
                .filter((p) => {
                  if (child !== '') {
                    if (p.personsCoordinates.length > 0) {
                      for (let c of p.personsCoordinates) {
                        return c.id === child;
                      }
                    }
                    return false;
                  }
                  return true;
                })
                .map((post, index) => {
                  return (
                    <div key={index} className={classes.postItem}>
                      <Post
                        post={post}
                        projectName={handleProjectName(post.project)}
                        handleOpenDialog={() => handleOpenDialog(post.id)}
                      />
                    </div>
                  );
                })
            ) : (
              <Typography variant="h6" color="textSecondary" component="p">
                Ainda não tem posts disponíveis para você. Em breve eles irão
                aparecer aqui!
              </Typography>
            )}
          </div>
        </Paper>
      </div>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    overflow: 'hidden',
    padding: '4vh 1vh',
    height: '100%',
  },
  profile: {
    paddingTop: '150px',
    textAlign: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
  },
  paper: {
    padding: theme.spacing(2),
    margin: isMobile ? 'auto' : '',
    marginLeft: isMobile ? '' : '10%',
    maxWidth: 550,
  },
  postItem: {
    marginBottom: '20px',
  },
  avatar: {
    width: theme.spacing(20),
    height: theme.spacing(20),
  },
  divBrowser: {
    flex: 2,
    textAlign: 'center',
  },
  avatarDiv: {
    alignItems: 'center',
    display: 'flex',
    justifyContent: 'center',
    marginBottom: '2vh',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  select: {
    textAlign: isMobile ? 'center' : 'end',
  },
}));

export default Home;
