import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import Paper from '@material-ui/core/Paper';

import { ReactComponent as Logo } from '../../assets/logoBlack.svg';
import { Container } from '@material-ui/core';

import SignIn from './SignIn';
import SignUp from './SignUp';

const Login = () => {
  const classes = useStyles();

  const [currentPage, setCurrentPage] = useState('SignIn');

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  return (
    <Container maxWidth="sm" className={classes.root}>
      <Paper className={classes.paper}>
        <div className={classes.container}>
          <Logo className={classes.logo} />
        </div>
        <div>
          {currentPage === 'SignIn' ? (
            <SignIn
              messageError={getAuthMessage}
              onPageChange={handlePageChange}
            ></SignIn>
          ) : (
            <SignUp
              messageError={getAuthMessage}
              onPageChange={handlePageChange}
            ></SignUp>
          )}
        </div>
      </Paper>
    </Container>
  );
};

const getAuthMessage = (rps) => {
  switch (rps.code) {
    case 'auth/email-already-exists':
    case 'auth/email-already-in-use':
      return 'E-mail já utilizado em outro cadastro :(';
    case 'auth/invalid-email':
      return 'Endereço de e-mail inválido :(';
    case 'auth/invalid-password':
    case 'auth/weak-password':
      return 'Senha inválida :( É necesssário pelo menos 6 caracteres.';
    case 'auth/wrong-password':
      return 'Senha incorreta :(';
    case 'auth/user-not-found':
      return 'Usuário não cadastrado :(';

    default:
      return rps.message;
  }
};

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  paper: {
    padding: theme.spacing(2),
    margin: 'auto',
    maxWidth: 500,
    textAlign: 'center',
  },
  logo: {
    width: '150px',
    height: '60px',
    paddingBottom: '5px',
  },
  container: {
    textAlign: 'center',
  },
}));

export default Login;
