import React, { useContext, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import {
  CardContent,
  CardActions,
  Collapse,
  Card,
  Chip,
  CardHeader,
  CardMedia,
  CardActionArea,
  CircularProgress,
  Avatar,
  IconButton,
  Typography,
} from '@material-ui/core';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ModeCommentIcon from '@material-ui/icons/ModeComment';
import { firestore } from '../../config/firebase';
import { UserContext } from '../../config/UserContext';

const Post = ({ post, projectName, handleOpenDialog }) => {
  const currentUser = useContext(UserContext);

  const [expanded, setExpanded] = useState(false);
  const [comments, setComments] = useState();
  const [ownerProps, setOwnerProps] = useState();
  const [like, setLike] = useState('#757575');
  const [docId, setDocId] = useState();
  const [showTag, setShowTag] = useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const handleClick = () => {
    console.info('You clicked the Chip.');
  };

  const handleFavorite = () => {
    if (!docId) {
      firestore.collection('likes').add({
        postId: post.id,
        uid: currentUser.uid,
      });
      setLike('#ff3838');
    } else {
      firestore.collection('likes').doc(docId).delete();
      setLike('#757575');
      setDocId(null);
    }
  };

  useEffect(() => {
    firestore
      .collection('users')
      .doc(post.uid)
      .get()
      .then((doc) => {
        if (doc.data()) {
          setOwnerProps({ name: doc.data().user, photo: doc.data().photo });
        }
      });
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    firestore
      .collection('comments')
      .where('postId', '==', post.id)
      .get()
      .then((snapshot) => {
        if (snapshot) {
          let data = [];
          snapshot.forEach((item) => {
            data.push({ ...item.data(), id: item.id });
          });
          setComments(data);
        }
      });

    firestore
      .collection('likes')
      .where('postId', '==', post.id)
      .where('uid', '==', currentUser.uid)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          if (doc.exists) {
            setDocId(doc.id);
            setLike('#ff3838');
          }
        });
      });
  }, [post]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleTagsOnImage = () => {};
  const classes = useStyles();
  return (
    <Card className={classes.root}>
      <CardHeader
        avatar={
          ownerProps ? (
            <Avatar
              aria-label="recipe"
              className={classes.avatar}
              src={ownerProps.photo}
            />
          ) : (
            <Avatar className={classes.avatar}>N</Avatar>
          )
        }
        action={
          <IconButton aria-label="settings">
            {/* <MoreVertIcon /> */}
          </IconButton>
        }
        title={ownerProps ? ownerProps.name : ''}
        // subheader={date}
      />

      {post.photo ? (
        <div>
          {post.personsCoordinates.map((data, index) =>
            data.name === 'UNKNOWN' ? (
              ''
            ) : (
              <div
                key={index}
                className={classes.tag}
                style={{
                  left: `${data.coordinates[0]}px`,
                  top: `${data.coordinates[1]}px`,
                  display: showTag ? '' : 'none',
                }}
              >
                {data.name}
              </div>
            )
          )}
          <CardActionArea onClick={() => setShowTag(!showTag)}>
            <CardMedia
              className={classes.media}
              image={post.photo}
              title=""
              id="card"
              onClick={handleTagsOnImage}
            />
          </CardActionArea>
        </div>
      ) : (
        <CircularProgress />
      )}
      <CardContent>
        {projectName && (
          <div className={classes.chipList}>
            <Chip
              className={classes.chip}
              size="small"
              label={'#' + projectName}
              onClick={handleClick}
            />
          </div>
        )}
        <Typography variant="body2" color="textSecondary" component="p">
          {/* {post.personsCoordinates[0].id} */}
          {post.personsCoordinates.map((data, index) => {
            return data.name === 'UNKNOWN' ? (
              ''
            ) : (
              <b className={classes.tagText} key={index}>
                @{data.name}
              </b>
            );
          })}
        </Typography>
        <Typography variant="body2" color="textSecondary" component="p">
          {post.description}
        </Typography>
        <Typography variant="caption" color="textSecondary" component="p">
          {new Date(post.date.seconds * 1000).toLocaleString()}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites" onClick={handleFavorite}>
          <FavoriteIcon style={{ fill: like }} />
        </IconButton>
        {/* <IconButton aria-label="share">
          <ShareIcon />
        </IconButton> */}
        <IconButton aria-label="comment" onClick={handleOpenDialog}>
          <ModeCommentIcon />
        </IconButton>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          {comments ? (
            comments.length > 0 ? (
              comments.map((data, index) => {
                return (
                  <div key={index}>
                    <Typography
                      variant="subtitle2"
                      color="textSecondary"
                    ></Typography>
                    <Typography variant="body2" color="textSecondary" paragraph>
                      <b>{data.user}</b> {data.comment}
                    </Typography>
                  </div>
                );
              })
            ) : (
              <Typography variant="body2" color="textSecondary" component="p">
                Nenhum comentário ainda, faça um :)
              </Typography>
            )
          ) : (
            ''
          )}
        </CardContent>
      </Collapse>
    </Card>
  );
};

/// outr build
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: 'hidden',
    position: 'relative',
    // padding: theme.spacing(10, 3),
    // height: "100%",
  },
  paper: {
    padding: theme.spacing(2),
    margin: 'auto',
    maxWidth: 500,
  },
  chip: {
    backgroundColor: '#f7ac97',
    margin: '0.5vh 1vh 0.5vh 0',
  },
  chipList: {
    display: 'flex',
    justifyContent: 'left',
    flexWrap: 'wrap',
    padding: '1vh 2vh 1vh 0',
  },
  media: {
    height: 200,
    paddingTop: '56.25%', // 16:9
    zIndex: 1,
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
  comment: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: '0px 15px 15px 15px',
  },
  tag: {
    position: 'absolute',
    zIndex: 3,
    padding: '5px 10px',
    border: '1px solid',
    left: ' 320px',
    top: '153px',
    borderRadius: '5px',
    borderColor: 'rgb(29, 24, 24 , 32%)',
    borderWidth: '0px ,6px, 6px',
    background: 'rgb(6, 5, 5 , 80%)',
    color: '#b7b7b7',
  },
  tagText: {},
}));

export default Post;
