import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { TextField, IconButton, Button, Paper } from '@material-ui/core';
import DoneIcon from '@material-ui/icons/Done';
import BackIcon from '@material-ui/icons/ArrowBackIos';
import PhotoCamera from '@material-ui/icons/PhotoCameraRounded';
import { firestore } from '../../config/firebase';
import configDB from '../../config/firebase';
import { Redirect } from 'react-router-dom';
import { show, changeMessage, changeStatus } from '../snackBar/snackBarSlice';
import { useDispatch } from 'react-redux';

const RegisterChild = ({ onPageChange }) => {
  const [name, setName] = useState('');
  const [age, setAge] = useState('');
  const [source, setSource] = useState('');
  const [file, setFile] = useState('');
  const [redirect, setRedirect] = useState(false);
  const dispatch = useDispatch();

  const savePost = async () => {
    let imageUrl = '',
      personId = '';
    const storageRef = configDB.storage().ref();
    const fileRef = storageRef.child(file.name);
    await fileRef.put(file).then(() => {
      console.log('Uploaded file', file.name);
    });

    await fileRef.getDownloadURL().then((response) => {
      console.log('deu certo!!!!');
      imageUrl = response;
    });

    if (imageUrl) {
      firestore
        .collection('children')
        .add({
          name: name,
          age: age,
          photo: imageUrl,
        })
        .then(function (docRef) {
          personId = docRef.id;

          var formdata = new FormData();
          formdata.append('picture', file, '/path/to/file');
          formdata.append('id', personId);
          formdata.append('name', name);

          var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow',
          };

          fetch('http://127.0.0.1:5000/person/add', requestOptions)
            .then(async (response) => console.log(await response.json()))
            .then(() => {
              dispatch(changeMessage('A criança foi cadastrada com sucesso!'));
              dispatch(changeStatus('success'));
              dispatch(show());
              setRedirect(true);
            })
            .catch((error) => console.log('error', error));
        });
    }
  };

  const handleCapture = async (target) => {
    if (target.files) {
      if (target.files.length !== 0) {
        const file = target.files[0];

        const newUrl = URL.createObjectURL(file);
        setFile(file);
        setSource(newUrl);
      }
    }
  };

  const classes = useStyles();
  return (
    <>
      {redirect ? <Redirect push to="/" /> : null}
      <div className={classes.root}>
        <div className={classes.div}>
          <TextField
            id="description"
            label="Nome"
            variant="outlined"
            fullWidth
            className={classes.textButton}
            onChange={(e) => setName(e.target.value)}
          />
        </div>
        <div className={classes.div}>
          <TextField
            id="outlined-number"
            label="Idade"
            variant="outlined"
            type="number"
            fullWidth
            className={classes.textButton}
            onChange={(e) => setAge(e.target.value)}
          />
        </div>

        {source !== '' && (
          <Paper className={classes.imgBox}>
            <img src={source} alt={'snap'} className={classes.img}></img>
          </Paper>
        )}

        <div className={classes.div}>
          <input
            accept="image/*"
            className={classes.input}
            id="icon-button-file"
            type="file"
            capture="environment"
            onChange={(e) => handleCapture(e.target)}
          />

          <label className={classes.label} htmlFor="icon-button-file">
            <Button
              component="span"
              variant="contained"
              className={classes.photoButton}
              fullWidth
              startIcon={<PhotoCamera />}
            >
              Tirar uma foto
            </Button>
          </label>
        </div>
        <div className={classes.div}>
          <div className={classes.buttonDiv}>
            <IconButton aria-label="voltar" onClick={() => onPageChange('')}>
              <BackIcon />
            </IconButton>
          </div>
          <div className={classes.buttonDiv}>
            <IconButton aria-label="delete" onClick={savePost}>
              <DoneIcon className={classes.doneButton} />
            </IconButton>
          </div>
        </div>
      </div>
    </>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: '10px',
  },
  div: {
    display: 'flex',
  },
  doneButton: {
    margin: '5px',
    fill: '#04bd0d',
  },
  buttonDiv: {
    flex: 1,
  },
  textButton: {
    margin: '10px',
  },
  alert: {
    margin: '15px 0px 15px 0px',
  },
  imgBox: {
    maxWidth: '100%',
    maxHeight: '80%',
    margin: '10px',
    display: 'flex',
    justifyContent: 'center',
  },
  img: {
    height: 'inherit',
    maxWidth: 'inherit',
  },
  media: {
    height: 200,
  },
  photoButton: {
    margin: '5px 5px 10px 5px',
    backgroundColor: '#db826e',
    color: '#ffffff',
    '&:hover': {
      backgroundColor: '#db826e',
      color: '#ffffff',
    },
  },
  label: {
    display: 'contents',
  },
  input: {
    display: 'none',
  },
}));

export default RegisterChild;
