import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Paper, Button } from '@material-ui/core';
import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';
import { useDispatch } from 'react-redux';
import { show, changeMessage, changeStatus } from '../snackBar/snackBarSlice';
import PhotoCamera from '@material-ui/icons/PhotoCameraRounded';

const Camera = ({ onPageChange, getBlob }) => {
  const dispatch = useDispatch();

  const classes = useStyles();

  const [image, setImage] = useState();
  const [cropper, setCropper] = useState();
  const [showImage, setShowImage] = useState(true);

  const handleCapture = (e) => {
    setShowImage(false);
    e.preventDefault();
    let files;
    if (e.dataTransfer) {
      files = e.dataTransfer.files;
    } else if (e.target) {
      files = e.target.files;
    }
    const reader = new FileReader();
    reader.onload = () => {
      setImage(reader.result);
    };
    reader.readAsDataURL(files[0]);
  };

  const getCropData = () => {
    if (typeof cropper !== 'undefined') {
      cropper.getCroppedCanvas({ width: 600, height: 600 }).toBlob((blob) => {
        getBlob(blob);
      });
      onPageChange('PhotoConfig');
    } else {
      dispatch(
        changeMessage('Não achamos nenhuma foto :( Tire uma para prosseguir!')
      );
      dispatch(changeStatus('error'));
      dispatch(show());
    }
  };

  return (
    <div>
      <div className={classes.divImg}>
        {showImage && (
          <img
            className={classes.media}
            src={require('../../assets/girl_with_phone.png')}
            alt="girl"
          />
        )}
      </div>
      <div className={classes.div}>
        <input
          accept="image/*"
          className={classes.input}
          id="icon-button-file"
          type="file"
          capture="environment"
          onChange={handleCapture}
        />

        <label className={classes.label} htmlFor="icon-button-file">
          <Button
            component="span"
            variant="contained"
            className={classes.photoButton}
            fullWidth
            startIcon={<PhotoCamera />}
          >
            Tirar uma foto
          </Button>
        </label>
      </div>
      {image && (
        <Paper className={classes.imgBox}>
          <div style={{ width: '100%' }}>
            <Cropper
              style={{ height: 400, width: '100%' }}
              zoomTo={0}
              aspectRatio={1}
              src={image}
              viewMode={1}
              maxWidth={1080}
              maxHeight={1080}
              guides={true}
              minCropBoxHeight={100}
              minCropBoxWidth={100}
              initialAspectRatio={1 / 1}
              background={false}
              responsive={true}
              autoCropArea={0}
              dragMode="move"
              checkOrientation={false}
              onInitialized={(instance) => {
                setCropper(instance);
              }}
            />
          </div>
        </Paper>
      )}
      <div>
        <div className={classes.div}>
          <Button
            component="span"
            variant="contained"
            className={classes.photoButton}
            fullWidth
            onClick={getCropData}
          >
            Cortar Imagem
          </Button>
        </div>
      </div>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  imgBox: {
    maxWidth: '100%',
    maxHeight: '80%',
    margin: '10px',
    display: 'flex',
    justifyContent: 'center',
  },
  div: {
    display: 'flex',
  },
  label: {
    display: 'contents',
  },
  input: {
    display: 'none',
  },
  photoButton: {
    margin: '5px 5px 10px 5px',
    backgroundColor: '#db826e',
    color: '#ffffff',
    '&:hover': {
      backgroundColor: '#db826e',
      color: '#ffffff',
    },
  },
  divImg: {
    alignItems: 'center',
    display: 'flex',
    justifyContent: 'center',
    marginBottom: '2vh',
  },
  media: {
    height: 300,
  },
}));

export default Camera;
