import React, { useEffect, useContext, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import { AppBar as AppBarMaterial } from '@material-ui/core';
import { Toolbar, IconButton } from '@material-ui/core';
import { Link } from 'react-router-dom';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

import { ReactComponent as Logo } from '../../assets/logo.svg';
import HomeIcon from '@material-ui/icons/Home';
import AccountCircle from '@material-ui/icons/AccountCircle';
import PhotoCamera from '@material-ui/icons/CameraAltSharp';
import AddCircleIcon from '@material-ui/icons/AddCircleSharp';

import { BrowserView } from 'react-device-detect';
import { auth } from '../../config/firebase';
import { UserContext } from '../../config/UserContext';
import { firestore } from '../../config/firebase';

const AppBar = () => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const isMenuOpen = Boolean(anchorEl);
  const currentUser = useContext(UserContext);
  const [admin, setAdmin] = useState(false);

  useEffect(() => {
    if (currentUser) {
      firestore
        .collection('users')
        .doc(currentUser.uid)
        .get()
        .then((doc) => {
          if (doc.data()) {
            setAdmin(doc.data().admin);
          }
        });
    }
  });

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBarMaterial className={classes.appBar} position="static">
      <Toolbar>
        <div className={classes.root}>
          {' '}
          <Logo className={classes.logo} />
        </div>
        <BrowserView>
          <Link to="/">
            <IconButton edge="end" className={classes.icon}>
              <HomeIcon />
            </IconButton>
          </Link>
          {admin && (
            <Link to="/camera">
              <IconButton edge="end" className={classes.icon}>
                <PhotoCamera />
              </IconButton>
            </Link>
          )}
          {admin && (
            <Link to="/register">
              <IconButton edge="end" className={classes.icon}>
                <AddCircleIcon />
              </IconButton>
            </Link>
          )}

          <IconButton
            edge="end"
            className={classes.icon}
            onClick={handleMenuOpen}
          >
            <AccountCircle />
          </IconButton>
          <Menu
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            open={isMenuOpen}
            onClose={handleMenuClose}
          >
            <MenuItem
              component={Link}
              to={'/profile'}
              onClick={handleMenuClose}
            >
              Profile
            </MenuItem>
            {/* <MenuItem onClick={handleMenuClose}>Configurações</MenuItem> */}
            <MenuItem onClick={() => auth.signOut()}>Sair</MenuItem>
          </Menu>
        </BrowserView>
      </Toolbar>
    </AppBarMaterial>
  );
};

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
  },
  appBar: {
    background: '#011936',
    width: '100%',
    position: 'fixed',
    top: '0',
  },
  logo: {
    width: '150px',
    height: '60px',
    paddingBottom: '5px',
  },
  icon: {
    color: 'white',
  },
  link: {
    textDecoration: 'none',
  },
});

export default AppBar;
